.form {
  display: flex;
  flex-direction: column;
  margin-top: 100px;
  margin-bottom: 50px;
  gap: 15px;
  width: 75%;
  max-width: 600px;
}

.button {
  width: 100%;
  display: flex;
  justify-content: center;
}

.button Button {
  min-width: 150px;
}

.form Input {
  display: flex;
  width: 100%;
}

.row {
  gap: 10px;
  display: flex;
  flex-direction: row;

  justify-content: center;
}

.specialitiesInput :hover {
  cursor: pointer;
}

.educationContainer {
  padding: 10px;
  border-radius: 5px;
  background: rgb(240, 240, 240);
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.country {
  flex: 1;
}
.city {
  flex: 4;
}
